/*
 * Typography
 */
.site-header-container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;
  padding: 3rem 0;
  position: relative; }

.entry-content {
  padding-top: 2rem;
  padding-bottom: 2rem; }

.site-main {
  padding-bottom: 3rem; }

.site-header {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: auto;
  color: var(--header-text-color, inherit);
  background-color: var(--header-bg-color, --base-bg-color); }

.site-branding {
  font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"); }
  .site-branding .custom-logo-link {
    display: block; }

.site-title {
  line-height: 1.1;
  margin: 0; }
  .site-title a {
    font-size: 3.25rem;
    font-weight: 400;
    text-decoration: none; }

.site-description {
  margin: 0 0 1em 0; }

.site-navigation {
  display: flex;
  justify-content: center; }

.site-footer {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: auto;
  color: var(--base-color, inherit);
  padding: 2rem;
  text-align: center; }

.site-info {
  padding: 0 2rem;
  font-size: .8rem; }

.widget {
  flex: 1;
  padding: 0 1rem;
  flex-basis: auto;
  margin: .5rem; }
  .widget-area {
    display: flex;
    padding: 2rem;
    flex-wrap: wrap; }
  .widget-title {
    margin: .8rem 0;
    font-size: 1.1rem;
    font-weight: 600;
    padding-bottom: .5rem;
    border-bottom: 1px solid; }
  .widget a {
    text-decoration: none;
    font-weight: 500; }
  .widget_search .search-field {
    margin-top: 1rem;
    margin-bottom: 0;
    border-radius: var(--base-border-radius, 3rem); }
  .widget_search .search-submit {
    margin-bottom: 1rem;
    width: 100%; }

input[type="submit"] {
  border-radius: var(--base-border-radius, 3rem);
  background-color: var(--base-color, #67c0c1);
  font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
  letter-spacing: var(--headings-letter-spacing, 0.015em);
  color: var(--base-bg-color);
  display: block;
  width: auto;
  margin: 0 auto;
  transition: all 300ms linear; }
  input[type="submit"]:hover, input[type="submit"]:active {
    background-color: var(--base-bg-color, #67c0c1);
    color: var(--base-color); }

.menu-toggle {
  width: 2rem;
  min-height: calc(2rem + 2px);
  position: absolute;
  left: 1rem;
  padding: 2rem;
  top: 2.5rem; }

.main-navigation {
  display: flex;
  align-items: center;
  position: relative; }
  .main-navigation .menu-container {
    display: flex;
    flex: 1;
    margin-left: 0rem;
    overflow: visible;
    justify-content: center; }
    .main-navigation .menu-container > .menu {
      margin-bottom: 0;
      list-style-type: none;
      padding-left: 0;
      display: flex; }
      .main-navigation .menu-container > .menu li a {
        line-height: 1em;
        display: block;
        text-decoration: none; }
        @media (min-width: 768px) {
          .main-navigation .menu-container > .menu li a {
            padding: 1rem 1.5rem;
            display: inline-block;
            font-weight: 400;
            font-size: 0.9rem;
            font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"); } }

.page-header {
  padding: 0 2rem; }

.archive .page-title {
  font-size: 1.5rem; }

.page-content {
  font-size: 1.7rem;
  padding: 0 2rem; }
  .page-content p {
    margin-bottom: 2em; }

.entry-title {
  font-size: 4rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  line-height: 1.1;
  padding: 1rem 2rem;
  border-bottom: 1px solid; }
  .entry-title a {
    text-decoration: none; }

.entry-header {
  margin-bottom: 1rem; }

.entry-meta, .entry-footer {
  padding: 0 2rem; }
  .entry-meta > span, .entry-footer > span {
    margin-right: 1rem;
    margin-top: .5rem;
    margin-bottom: .5rem;
    padding: .5rem 1rem;
    border: 1px solid;
    font-weight: 400;
    font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
    letter-spacing: var(--headings-letter-spacing, 0.015em);
    border-radius: var(--base-border-radius, 3rem);
    display: inline-block;
    font-size: 80%; }
    .entry-meta > span a, .entry-footer > span a {
      transition: all 300ms linear;
      text-decoration: none; }
    .entry-meta > span:hover, .entry-footer > span:hover {
      background-color: var(--base-color);
      color: var(--base-bg-color); }
      .entry-meta > span:hover a, .entry-footer > span:hover a {
        color: var(--base-bg-color);
        letter-spacing: .04em; }

.entry-date + time {
  display: none; }

.comments-area {
  padding-left: 2rem;
  padding-right: 2rem; }

.comment-list {
  padding-left: 2rem;
  font-size: 2rem; }

.comments-title {
  border-bottom: 1px solid;
  padding-bottom: 1rem; }

.comment-content {
  padding: 1rem;
  display: block;
  margin: 1rem 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: .35rem;
  background-color: rgba(255, 255, 255, 0.6); }

.comment .reply {
  margin-right: 1rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  padding: .5rem 1rem;
  border: 1px solid;
  font-weight: 400;
  font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
  letter-spacing: var(--headings-letter-spacing, 0.015em);
  border-radius: var(--base-border-radius, 3rem);
  display: inline-block;
  font-size: 80%; }
  .comment .reply a {
    transition: all 300ms linear;
    text-decoration: none; }
  .comment .reply:hover {
    background-color: var(--base-color);
    color: var(--base-bg-color); }
    .comment .reply:hover a {
      color: var(--base-bg-color);
      letter-spacing: .04em; }

.comment-meta {
  display: flex; }

.comment-metadata {
  font-size: 80%;
  margin-top: 0; }
  .comment-metadata > a, .comment-metadata > span {
    margin-right: 1rem;
    margin-top: .5rem;
    margin-bottom: .5rem;
    padding: .5rem 1rem;
    border: 1px solid;
    font-weight: 400;
    font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
    letter-spacing: var(--headings-letter-spacing, 0.015em);
    border-radius: var(--base-border-radius, 3rem);
    display: inline-block;
    font-size: 80%;
    margin-right: 0;
    margin-top: 0;
    margin-bottom: 0; }
    .comment-metadata > a a, .comment-metadata > span a {
      transition: all 300ms linear;
      text-decoration: none; }
    .comment-metadata > a:hover, .comment-metadata > span:hover {
      background-color: var(--base-color);
      color: var(--base-bg-color); }
      .comment-metadata > a:hover a, .comment-metadata > span:hover a {
        color: var(--base-bg-color);
        letter-spacing: .04em; }

.comment-author {
  flex: 1; }
  .comment-author .avatar {
    margin-right: .5rem; }
  .comment-author .fn {
    font-weight: 600;
    display: inline-block; }
  .comment-author .says {
    font-size: 80%;
    display: inline-block; }

.comment-author a, .comment-metadata a {
  text-decoration: none;
  font-weight: 500; }

.comment-body {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  padding: 1rem; }

.comment-respond {
  font-size: 1rem; }

.comment-submit {
  border-radius: var(--base-border-radius, 3rem);
  background-color: var(--base-color, #67c0c1);
  font-family: var(--headings-font-family, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
  letter-spacing: var(--headings-letter-spacing, 0.015em);
  color: var(--base-bg-color);
  display: block;
  width: auto;
  margin: 0 auto;
  transition: all 300ms linear; }
  .comment-submit:hover, .comment-submit:active {
    background-color: var(--base-bg-color, #67c0c1);
    color: var(--base-color); }

textarea {
  overflow: auto;
  resize: vertical;
  border-radius: 1rem;
  background-color: #fbfbfb45; }

.logged-in-as > a {
  text-decoration: none;
  font-weight: 600; }

.wp-block-cover, .wp-block-image {
  margin-bottom: 2rem; }

.wp-block-cover {
  padding: 2rem 0; }
  .wp-block-cover .wp-block-cover__inner-container {
    max-width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    width: var(--content-width, 960px); }
    @media (min-width: 768px) {
      .wp-block-cover .wp-block-cover__inner-container {
        padding-left: 2rem;
        padding-right: 2rem; } }
  .wp-block-cover h2, .wp-block-cover h3, .wp-block-cover h4, .wp-block-cover h5, .wp-block-cover h6, .wp-block-cover p {
    margin: 0 auto; }

.area-inverted {
  background-color: var(--base-color, #67c0c1);
  color: var(--base-bg-color, #f6f7f9); }
  .area-inverted a {
    color: var(--base-bg-color, #f6f7f9); }
  .area-inverted button, .area-inverted [type="submit"] {
    background-color: var(--base-color-darker, #409b9c); }
